<template>
	<div class="container">
		<scroll-bar />
		<article v-for='(article, index) in articles' :key='index' class="single-article">
			<div v-if='$_friendlyUrl(article.title) === proId'>
				<div class="single-article__title">
				    {{article.title}}
				</div>
				<!-- /.single-article__title -->
				<div class="single-article__img">
				    <img :src="article.img" :alt="article.title" class="img-responsive">
				</div>
				<!-- /.single-article__img -->
				<div class="single-article__meta">
				    <span class="date">
				        <i class="fas fa-calendar-alt"></i>
				       {{article.createdAt.seconds | moment("DD.MM.YY")}}
				    </span>
				</div>
				<!-- /.single-article__meta -->
				<div class="single-article__content" v-html='article.content'></div>
				<!-- /.single-article__content -->
				<div class="single-article__share">
					<p>
						Сподобалась публікація? Поділись нею з друзями :)
					</p>
					<facebook
					    :page_url='currentUrl'
					    title_social="Facebook"
					    has_icon
					  ></facebook>
				  	<telegram
			      		:page_url='currentUrl'
			      		title_social="Telegram"
			      		has_icon
			      	></telegram>
			      	<twitter
			      		:page_url='currentUrl'
			      		title_social="Twitter"
			      		has_icon
			      	></twitter>
				</div>
				<!-- /.single-article__share -->
				<div class="comments">
				    <vue-disqus shortname="personal-12" :identifier="article.id" :url="currentUrl"></vue-disqus>
				  </div>
			</div> 
		</article>
		<!-- /.single-article -->
	</div>
	<!-- /.container -->
</template>

<script>
	const Facebook = () => import("vue-goodshare/src/providers/Facebook.vue");
	const Telegram = () => import("vue-goodshare/src/providers/Telegram.vue");
	const Twitter = () => import("vue-goodshare/src/providers/Twitter.vue");

	const ScrollBar = () => import("../ScrollBar");

	export default {
		data() {
			return {
				proId: this.$route.params.title,
				articles: [],
				currentUrl: window.location.href,
				current: ''
			}
		},
		metaInfo () {
	      	return {
	        	title: this.currentArticle().title,
	       		meta: [
	       			{
	       				name: 'robots',
	       				content: 'index,follow,noodp'
	       			},
	       			{
	       				name: "googlebot",
	       				content: "index,follow"
	       			},
	       			{
	       				property: 'og:locale',
	       				content: 'uk_UK',
	       			},
	       			{
	       				property: 'og:type',
	       				content: 'article',
	       			},
	       			{
	       				property: 'og:title',
	       				content: this.currentArticle().title,
	       			},
	       			{
	       				property: 'og:url',
	       				content: this.currentUrl,
	       			},
	       			{
	       				property: 'og:site_name',
	       				content: 'Сайт Юрія Колцьо',
	       			},
	          		{ 
	          			property: 'og:description', 
	          			content: this.currentArticle().excerpt 
	          		},
	          		{	
	          			property: 'og:image',
	          			content: this.currentArticle().img 
	          		},
	          		{
	          			property: 'twitter:image',
	          			content: this.currentArticle().img 
	          		},
	          		{
	          			property: 'twitter:title',
	          			content: this.currentArticle().title,
	          		},
	          		{
	          			property: 'twitter:description',
	          			content: this.currentArticle().excerpt,
	          		},
	        	]
	      }
	    },
		methods: {
			currentArticle() {
				this.articles.forEach((item) => {
					if (this.$_friendlyUrl(item.title) === this.proId) {
						this.current = item;
					}
				});
				return this.current;
			}
		},
		firestore () {
		   return {
		     articles: this.$_db.collection('blog').orderBy('createdAt')
		   }
		},
		components: {
		   Facebook,
		   Telegram,
		   Twitter,
		   ScrollBar
		}
		
	}
</script>
